.homeHeader{
  background-color: #1346ff;
  color: #fff;
  font-size: 24px;
  line-height: 60px;
  padding: 2px 16px;
  position: absolute;
  top: 0;
  z-index: 99;
  width: 100%;
}

.root {
  // position: absolute;
  // left: 0;
  // top: 0;
  // right: 0;
  z-index: 9;
  background-color: #1346ff;
  border-bottom: 1px solid #1346ff;
  &.light{
    background-color: #F9FAFC;
    border-bottom: 1px solid #EEEEEE;
    .connectBtn{
      background-color: #1346FF;
    }
    &.mobileLight{
      :global{
        .ant-menu-item, .ant-menu-submenu-title{
          padding: 0 15px;
        }
      }
    }
    :global{
      .ant-menu{
        background-color: #F9FAFC;
        color: #333333;
        border-bottom-color: #EEEEEE;

        .ant-menu-item:hover {
          border-bottom-color: transparent;
        }
        .ant-menu-item.ant-menu-item-selected{
          border-bottom-color: #1346ff;
        }

        .ant-menu-submenu-title:hover,
        &:not(.ant-menu-inline) .ant-menu-submenu-open, 
        .ant-menu-submenu-active,
        &.ant-menu-horizontal > .ant-menu-submenu-selected,
        &.ant-menu-horizontal > .ant-menu-submenu:hover{
          color: #333;
        }
        &.ant-menu-horizontal > .ant-menu-item > a {
          color: #333333;
        }
      }
    }
  }

  .connectWpr{
    text-align: right;
  }
  .connectBtn {
    height: 40px;
    background: rgba(229, 238, 255, 0.14);
    font-size: 16px;
    margin-right: 40px;
    background: rgba(229, 238, 255, 0.14);
    color: #FFF;
    border: 1px solid #3b6cff;
  }

  :global {
    .ant-menu-horizontal {
      border-bottom: 0;
      line-height: 60px;
    }
    .ant-menu-item {
      &:hover,
      &-active,
      &-open,
      &-selected {
        border-bottom-color: #1346ff;
      }
    }
  }
}

.dderivatives {
  font-size: 24px;
  padding-left: 42px;
}

