.root {
  &.mobile{
    background-color: #F9FAFC;
    .currPrice, .headArea{
      padding-left: 16px;
      :global{
        .ant-select-selection-selected-value{
          font-size: 18px;
        }
      }
    }
    .range{
      text-align: left;
    }
    :global{
      .ant-select-selection{
        background-color: transparent;
      }
    }
  }
  .currPrice, .headArea{
    padding-left: 50px;
  }
  .currVal {
    font-size: 36px;
    font-weight: bold;
    color: #333333;
  }
  .change {
    font-size: 14px;
    font-weight: 500;
    color: #ff6666;

    span {
      color: #333;
    }
  }
  .range {
    text-align: right;
    button {
      font-size: 14px;
      font-weight: 500;
      color: #333333;

      &.current{
          color: #1346FF;
      }
    }

  }

  :global{
    .ant-select-selection{
      border-color: transparent;
      box-shadow: none;
      &:focus,
      &:active{
        border-color: transparent;
        box-shadow: none;
      }
    }
  }
}

