.tradeInfoPool {
  &.mobile{
    margin-bottom: 60px;
    margin-top: 30px;
    .chartBalance{
      margin: 0;
    }
  }
  max-width: 1200px;
  margin: 48px auto 140px;
  .chartBalance {
    margin-bottom: 80px;

    & > div {
      height: 550px;
    }
    .charWpr {
      background-color: #fff;
      border-radius: 8px;
    }
  }
}

.modal {
  text-align: center;
  :global{
    .ant-modal-body{
      padding-top: 0;
      padding-bottom: 64px;
    }
    .ant-modal-header{
      padding: 36px 0 28px;
    }
    .ant-modal-footer{
      padding-top: 0;
      padding-bottom: 24px;
    }
  }
  .agree {
    margin: 22px 0 0;
    color: #F55858;
  }
}
.modal p {
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  line-height: 21px;
  margin: 0;
  
}

